@use "../../assets/scss/util" as *;

.Nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--white);
  padding-block: 1rem;
  padding-inline: 2rem;
  clip-path: circle(10rem at 50% 0);
  -webkit-clip-path: circle(10rem at 50% 0);
  transition: all 1s ease-in-out;

  @include breakpoint-down(medium) {
    padding-inline: 1rem;
  }

  position: fixed;
  width: 100vw;
  top: 0;

  &__BrandLink {
    block-size: 2rem;
    inline-size: 2rem;
  }

  &__hamburger {
    display: none;
  }

  &__hamburgerLine {
    block-size: 2px;
    inline-size: 20px;
    background-color: var(--grey-500);
    margin-block: 4px;
  }

  &__menu {
    display: flex;
    gap: 1rem;
    color: var(--grey-500);

    @include breakpoint(medium) {
      font-size: 0.6rem;
    }
  }

  &__menuMobile {
    display: none;
  }

  @include breakpoint-down(small) {
    align-items: baseline;

    &__mobile {
      height: 100%;
      clip-path: circle(50rem at 50% 0);
      -webkit-clip-path: circle(50rem at 50% 0);
      transition: all 1s ease-out;
    }

    &__hamburger {
      display: block;
    }

    &__menu {
      display: none;
      gap: 2rem;
    }

    &__menuMobile {
      display: flex;
      position: absolute;
      flex-direction: column;
      inline-size: calc(100% - 2rem);
      text-align: center;
      top: 6rem;
      height: calc(100% - 14rem);
      background: white;
      justify-content: center;
      align-items: center;

      a {
        inline-size: min-content;
      }
    }
  }
}
