:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);

  --grey-900: hsl(0, 0%, 13%);
  --grey-700: hsl(0, 0%, 38%);
  --grey-500: hsl(0, 0%, 62%);
  --grey-400: hsl(0, 0%, 74%);
  --grey-100: hsl(0, 0%, 96%);

  --primary-050: hsl(205, 87%, 94%);
  --primary-100: hsl(207, 89%, 86%);
  --primary-500: hsl(207, 90%, 54%);
  --primary-700: hsl(210, 79%, 46%);
}
